.phones {
  @include create-grid;
  padding-top: 24px;

  &__path {
    @include full-grid;
  }

  &__title {
    @include full-grid;
    color: $white;
    margin-top: 24px;

    @include on-tablet {
      margin-top: 40px;
    }
  }

  &__count {
    @include full-grid;
    margin-top: 8px;
    color: $secondary;
  }

  &__cards {
    @include full-grid;
    display: flex;
    flex-wrap: wrap;
    gap: 40px 16px;
  }
}
