.card {
  box-sizing: border-box;
  background-color: $surface-1;
  padding: 32px;
  gap: 8px;
  display: flex;
  flex-direction: column;
  justify-content: end;
  position: relative;
  width: 212px;
  height: 439px;

  @include on-tablet {
    width: 237px;
    height: 512px;
  }

  @include on-desktop {
    width: 272px;
    height: 506px;
  }

  &__image {
    cursor: pointer;
    width: 148px;
    height: 129px;
    object-position: center;
    object-fit: contain;
    position: absolute;
    top: 32px;

    @include on-tablet {
      width: 173px;
      height: 202px;
    }

    @include on-desktop {
      width: 208px;
      height: 196px;

    }
  }

  &__title {
    cursor: pointer;
    padding-top: 16px;
    color: $white;
  }

  &__prices {
    display: flex;
    gap: 8px;
    padding-bottom: 8px;
    border-bottom: 1px solid $elements;
  }

  &__price {
    color: $white;
  }

  &__old-price {
    text-decoration: line-through;
    color: $secondary;
  }

  &__info {
    padding-block: 8px;
    display: flex;
    flex-direction: column;
    gap: 8px;

    &-item {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &-param {
      color: $secondary;
    }

    &-value {
      color: $white;
    }
  }

  &__buttons {
    display: flex;
    gap: 8px;
    align-items: center;
  }

  &__favourite {
    display: inline-block;
    background-image: url('../../img/Icons/Favourites.png');
    background-size: cover;
    width: 16px;
    height: 16px;
    margin: 12px;

    &-block {
      display: inline-block;
      height: 40px;
      width: 40px;
    }

    &-selected {
      background-image: url('../../img/Icons/Favourites-Filled.png');
    }
  }
}
